import React, { useState, useEffect } from 'react';
import { Table, Button, message } from 'antd';

const ApprovalPage = ({ tools, setTools }) => {
  const [records, setRecords] = useState([]);
  const [loading, setLoading] = useState(false);
  const token = localStorage.getItem('token');

  const fetchPendingRecords = async () => {
    setLoading(true);
    try {
      const res = await fetch('http://localhost:5000/api/approvals', {
        headers: { 
          'Authorization': 'Bearer ' + token,
          'Content-Type': 'application/json'
        }
      });
      if (!res.ok) throw new Error(res.statusText);
      const data = await res.json();
      setRecords(data);
    } catch (err) {
      message.error('Failed to load approvals: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleApprove = async (id, action) => {
    try {
      // 
      const record = records.find(r => r.id === id);
      if (!record) {
        message.error('');
        return;
      }
      
      console.log(':', record);
      console.log('ID:', record.tool_id);
      
      // 
      const res = await fetch(`http://localhost:5000/api/approvals/${id}`, {
        method: 'PUT',
        headers: { 
          'Authorization': 'Bearer ' + token,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ status: action })
      });
      
      if (!res.ok) {
        throw new Error(`: ${res.status}`);
      }
      
      const result = await res.json();
      message.success(`application${action === 'approved' ? 'approved' : 'rejected'}`);
      
      // 
      if (action === 'approved') {
        // 
        const toolsRes = await fetch('http://localhost:5000/api/tools', {
          headers: { 'Authorization': 'Bearer ' + token }
        });
        
        if (!toolsRes.ok) {
          throw new Error('');
        }
        
        const toolsData = await toolsRes.json();
        setTools(toolsData); // 
        
        console.log('');
      }
      
      // 
      fetchPendingRecords();
      
    } catch (err) {
      console.error(':', err);
      message.error(`action failed: ${err.message}`);
    }
  };

  useEffect(() => {
    fetchPendingRecords();
  }, []);

  const columns = [
    {
      title: 'tool name',
      dataIndex: ['Tool', 'name'],
      key: 'tool_name',
    },
    {
      title: 'username',
      dataIndex: ['User', 'username'],
      key: 'user',
    },
    {
      title: 'project',
      dataIndex: ['Project', 'name'],
      key: 'project',
    },
    
    {
      title: 'action',
      key: 'action',
      render: (_, record) => (
        <div style={{ display: 'flex', gap: '8px' }}>
          <button 
            onClick={() => handleApprove(record.id, 'approved')}
            style={{ 
              backgroundColor: '#1890ff', 
              color: 'white', 
              border: 'none',
              padding: '4px 15px',
              borderRadius: '2px',
              cursor: 'pointer'
            }}
          >
            approve
          </button>
          <button 
            onClick={() => handleApprove(record.id, 'rejected')}
            style={{ 
              backgroundColor: '#ff4d4f', 
              color: 'white', 
              border: 'none',
              padding: '4px 15px',
              borderRadius: '2px',
              cursor: 'pointer'
            }}
          >
            reject
          </button>
        </div>
      ),
    },
  ];

  return (
    <div className="approval-page" style={{ padding: 24 }}>
      <h2 style={{ marginBottom: '20px', fontSize: '24px', fontWeight: 'bold' }}>Records to be approved</h2>
      <Table 
        columns={columns} 
        dataSource={records} 
        rowKey="id"
        loading={loading}
        pagination={{ 
          pageSize: 10,
          showSizeChanger: false,
          showQuickJumper: false,
          size: 'default',
          itemRender: (page, type, originalElement) => {
            if (type === 'prev') {
              return <button style={{ 
                padding: '5px 10px', 
                margin: '0 5px', 
                border: '1px solid #d9d9d9', 
                borderRadius: '2px',
                background: 'white',
                cursor: 'pointer'
              }}></button>;
            }
            if (type === 'next') {
              return <button style={{ 
                padding: '5px 10px', 
                margin: '0 5px', 
                border: '1px solid #d9d9d9', 
                borderRadius: '2px',
                background: 'white',
                cursor: 'pointer'
              }}></button>;
            }
            if (type === 'page') {
              return <button style={{ 
                padding: '5px 10px', 
                margin: '0 5px', 
                border: page === originalElement.props.current ? '1px solid #1890ff' : '1px solid #d9d9d9', 
                borderRadius: '2px',
                background: 'white',
                cursor: 'pointer',
                color: page === originalElement.props.current ? '#1890ff' : 'inherit'
              }}>{page}</button>;
            }
            return originalElement;
          }
        }}
        bordered
        style={{ boxShadow: 'none' }}
      />
    </div>
  );
};

export default ApprovalPage;
